import {getSuggestion,deleteSuggestion} from '../queries';
import { checkResponse } from "../../constants/index";

/* LISTCLIENT */
export const get_suggestion_list = payload => async dispatch => {
  let response = await getSuggestion(payload);
  response = checkResponse(response);

  if (response.success) {
    dispatch({
      type: "SUGGESTION_DATA",
      payload: response.data.data
    });
  } else {
    dispatch(
      {
        type: "ERROR_OCCURED",
        payload: response.error
      }
    )
  }

};

export const delete_suggestion = payload => async dispatch => {
  let response = await deleteSuggestion(payload);
  response = checkResponse(response);

  if (response.success) {
    dispatch(get_suggestion_list())
  } else {
    dispatch(
      {
        type: "ERROR_OCCURED",
        payload: response.error
      }
    )
  }

};






